import { Network } from '@ionic-native/network/ngx';
import { Injectable } from '@angular/core';
import { CargaElementosUsuariosService } from '../cargas/usuarios.service';
import { Socket } from 'ngx-socket-io';
import { ModalController } from '@ionic/angular';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class ServicioRedService {

  constructor(
    private red: Network,
    private cargarElementos: CargaElementosUsuariosService,
    private socket: Socket,
    private modalCtrl: ModalController,
    private router: Router
  ) { 
    this.conectarRed();
  }


  conectarRed() {
    return this.red.onChange().subscribe((e) => {
      console.log(this.red.type, ' Tipo de red')
      if (this.red.type === 'none') {
        this.sacarModal();
      } else if (this.red.type !== 'none'){
          this.cerrar();
      }
      this.cargarElementos.cargarRed(this.red.type);
    })
  }

  sacarModal() {
    // this.router.navigate
  }


  cerrar() {
    this.modalCtrl.dismiss();
  }

  cancelarServicio() {
    this.conectarRed().unsubscribe();
  }
}
