import { Component, Input, OnInit } from '@angular/core';
import { ActionSheetController, ModalController } from '@ionic/angular';
import { Socket } from 'ngx-socket-io';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { ToastServices } from '../../providers/Utilidades/ToastService.service';

import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { LoadingService } from '../../providers/Utilidades/LoadingService.service';
import { PedidosService } from '../../providers/Api/Pedidos.service';
declare var window: any;

@Component({
  selector: 'app-modal-chat',
  templateUrl: './modal-chat.component.html',
  styleUrls: ['./modal-chat.component.scss'],
})
export class ModalChatComponent implements OnInit {

  carga = false;
 
 slide = {
   zoom: {
     maxRation: 8,
   }
 }
 @Input() img;


  constructor(
    private almacenamiento: InicioService,
    private modal: ModalController,
    private toast: ToastServices,
    private socket: Socket,
    private actionCtrl: ActionSheetController,
    private camera: Camera,
    private myLoading: LoadingService,
    private apiPedido: PedidosService
  ) { }

  ngOnInit() {}

  ionViewWillEnter() {
    this.crear();
  }

  async crear() {

    const info = await this.almacenamiento.obtenerToken();

    if (info) {
      this.carga = true;
    }

  }



  cerrar() {
    this.modal.dismiss();
  }

}
