import { Injectable } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { RecordatoriosService } from '../Api/Recordatorios.service';
import { InicioService } from '../Almacenamiento/inicio.service';
import { EntradasComponent } from '../../Components/entradas/entradas.component';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})

export class EntradasServices {

    private inicio = new BehaviorSubject('');
    public $inicio = this.inicio.asObservable();
    constructor(
        private modalCtrl: ModalController,
        private apiRecordatorio: RecordatoriosService,
        private almacenamiento: InicioService
    ) {

    }

    alerta(txt) {
        this.inicio.next(txt)
    }

    async crearEntrada() {
        const info = await this.almacenamiento.obtenerToken();

        if (info) {
            this.apiRecordatorio.getRecordatorioPersona(info.token).subscribe((data: any) => {
                console.log('Data del servicio entrada', data);
                if (data.exe) {
                    data.response.forEach(async (item: any, ind) => {
                       console.log('DANDOME LA VUELTA*******************')
                       if (item.destinatarios.find((dest: any) => dest.id === info._id && dest.visto === 'NO')) {
                        const modal = await this.modalCtrl.create({
                            component: EntradasComponent,
                            componentProps: {
                                type: 'recordatorio',
                                data: item
                            }
                        })
                
                        await modal.present();

                        const finalice = await modal.onWillDismiss();

                        if ( finalice ) {
                            console.log('Siguiente', ind)
                        }
                       }
                    })
                }
            });
        }
    }

    async mostrarSoloUna(recor) {
        const modal = await this.modalCtrl.create({
            component: EntradasComponent,
            componentProps: {
                type: 'recordatorio',
                data: recor
            }
        })

        await modal.present();
    }
}
