import { Injectable } from '@angular/core';
import { URL_SERVER } from '../../../environments/environment';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { map, retryWhen, delayWhen, take, catchError } from 'rxjs/operators';
import { timer, Observable, from } from 'rxjs';
import { FileTransferObject, FileTransfer, FileUploadOptions } from '@ionic-native/file-transfer/ngx';
import { Router } from '@angular/router';


