    constructor(private http: HttpClient, private router: Router, private MmyTransfer: FileTransfer) { }

    private hanflerError(error: Observable<any>) {

            let intento = 0;

            return error.pipe(
                delayWhen(() => timer(3000)),
                map((errorHttp: HttpErrorResponse) => {

                        console.log('Entramos a intentos');
                        if (errorHttp instanceof ErrorEvent) {
                            throw new Error('Error de Red');
                        } else {

                            if ( errorHttp.status === 404) {
                               throw new Error(' Pgina no encontrada');
                            } if ( errorHttp.status === 401) {
                                throw new Error(' No tiene permisos ');
                             } else {
                               // throw new Error(' No se pudo realizar la conexin, comprueba tu red ');
                            }
                           
                        }
                }),
                take(3)
            );
    }

  



    crearPedido(token: any, data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/pedidos/crear/`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


