    subirArchivosChat(token, id, imagen, final, cont, name?, mime?) {
        console.log('archivo', imagen)
        const options: FileUploadOptions = {
            fileKey: 'archivo',
            fileName: name,
            mimeType: mime,
            httpMethod: 'PUT',
            headers: {
                'x-token': token,
                'x-sala': id,
                'x-final': final,
                'x-cont': cont
            }
        }

        const fileTransfer: FileTransferObject = this.MmyTransfer.create();

        return from(fileTransfer.upload(imagen, `${URL_SERVER}/pedido/upload`, options)).pipe(
            catchError(error => {
               // console.log(`No se pudo subir la imagen `, error);
                throw new Error(imagen);
            })
        )
        
    }

