import { NgModule, Output } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { RegistroGuard } from './Guard/login/registro.guard';
import { InicioGuard } from './Guard/login/inicio.guard';
import { CentralGuard } from './Guard/login/central.guard';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home', loadChildren: () => import('./pages/home/home.module').then( m => m.HomePageModule), canActivate: [InicioGuard]},

  // tslint:disable-next-line:max-line-length
  { path: 'registro', loadChildren: () => import('./pages/registro/registro.module').then( m => m.RegistroPageModule), canDeactivate: [RegistroGuard], canActivate: [InicioGuard], data: { animation: 'Registropage'}},

  // tslint:disable-next-line:max-line-length
  { path: 'central', loadChildren: () => import('./pages/central/central.module').then( m => m.CentralPageModule), canActivate: [CentralGuard]},

  {
    path: 'chat/:id',
    loadChildren: () => import('./Pages/EstadoCitas/chat/chat.module').then( m => m.ChatPageModule), canActivate: [CentralGuard]
  },
  {
    path: 'igtmpublic/:id/:tipo',
    loadChildren: () => import('./Pages/Modales/igtmpublic/igtmpublic.module').then( m => m.IgtmpublicPageModule)
  },


  {
    path: 'visita/:usuario/:tipo',
    loadChildren: () => import('./Pages/usuario/perfil-visita/perfil-visita.module').then( m => m.PerfilVisitaPageModule)
  },

  {
    path: 'perfil/:tipo',
    loadChildren: () => import('./Pages/usuario/perfil/perfil.module').then( m => m.PerfilPageModule)
  },
  {
    path: 'hoja-vida-modal/:id/:tipo/:hoja',
    loadChildren: () => import('./Pages/Modales/hoja-vida-modal/hoja-vida-modal.module').then( m => m.HojaVidaModalPageModule)
  },
  {
    path: 'hoja-vida-modal/:id/:tipo/:hoja/:idx',
    loadChildren: () => import('./Pages/Modales/hoja-vida-modal/hoja-vida-modal.module').then( m => m.HojaVidaModalPageModule)
  },

  {
    path: 'historias-modal',
    loadChildren: () => import('./Pages/Santiago/historias-modal/historias-modal.module').then( m => m.HistoriasModalPageModule)
  },

   {
            path: 'historias/:tipo',
            loadChildren: () => import('./Pages/Santiago/sancreador/sancreador.module').then( m => m.SANcreadorPageModule)
          },
  {
    path: 'redmodal',
    loadChildren: () => import('./Pages/redmodal/redmodal.module').then( m => m.RedmodalPageModule)
  },
 


];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
