import { Injectable } from '@angular/core';
import { ApiSeguimientoService } from '../Api/Seguimiento.service';
import { InicioService } from '../Almacenamiento/inicio.service';
import { BehaviorSubject, Subject, ReplaySubject } from 'rxjs';
import { find, last, takeLast } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CargaElementosUsuariosService {

  private seguimientoBehaviour = new BehaviorSubject(0);
  public  $seguimientoCantidad = this.seguimientoBehaviour.asObservable();

  private cargared = new BehaviorSubject('wifi');
  public $cargarRed = this.cargared.asObservable();

  private cancelarSeguimiento: any;

  constructor(
    private apiSeguimiento: ApiSeguimientoService,
    private almacenamiento: InicioService
  ) {

  }

  cargarSeguimientos() {
    this.almacenamiento.obtenerToken().then((info: any) => {
      this.cancelarSeguimiento = this.apiSeguimiento.solicitudesRecibidas(info.token).subscribe((data: any) => {
        this.seguimientoBehaviour.next(data.response.SeguimientoData.length);
        console.log(data.response.SeguimientoData, ' seguimientos cant');
      }, (err) => {
        console.log('Error en la solicitud');
      });
    });
  }


  cancelarSuscripciones() {
    this.cancelarSeguimiento.unsubscribe();
  }

  cargarRed(red: string) {
      console.log('Mi red', red);
      this.cargared.next(red);
  }

}
