import { Component, Input, OnInit } from '@angular/core';
import { AlertController, ModalController, PopoverController } from '@ionic/angular';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { IGTMService } from 'src/app/providers/Api/IGTM.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';

@Component({
  selector: 'app-alertapopover',
  templateUrl: './alertapopover.component.html',
  styleUrls: ['./alertapopover.component.scss'],
})
export class AlertapopoverComponent implements OnInit {

  @Input() id;

  constructor(
    private almacenamiento: InicioService,
    private modalCtrl: ModalController,
    private toast: ToastServices,
    private apiIgtm: IGTMService,
    private alert: AlertController,
    private popover: PopoverController
  ) { }

  ngOnInit() {
  }

  async alerta() {
    const alert = await this.alert.create({
      header: 'Esta seguro de eliminar esta publicacin?',
      message: 'esta accin ser de forma permanente',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel'
        }, {
          text: 'Aceptar',
          handler: () => {
            this.eliminar();
          }
        }
      ]
    })

    await alert.present();
  }

  async eliminar() {

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.apiIgtm.eliminar(info.token, {id: this.id}).subscribe((data: any) => {
        if (data.exe) {
          this.popover.dismiss({
            exe: true
          })
        } else {
          this.toast.toastError('No se pudo eliminar la publicacin, intentelo mpas tarde');
        }
      }, (err) => {
        this.toast.toastError('No se pudo eliminar la publicacin, intentelo mpas tarde');
      })
    }

  }


}

