import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { UsuariosLoginService } from '../../providers/Api/UsuariosLogin.service';
import { ToastServices } from '../../providers/Utilidades/ToastService.service';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { PatinadasService } from '../../providers/Api/Patinadas.service';
import { Socket } from 'ngx-socket-io';

@Component({
  selector: 'app-modal-patinada',
  templateUrl: './modal-patinada.component.html',
  styleUrls: ['./modal-patinada.component.scss'],
})
export class ModalPatinadaComponent implements OnInit {

  descripcion = '';

  paises= [];
  ciudades = [];
  ciudad = false;
  load = false;

  pais = '';
  miciudad = '';

  vista = 0;

  selecionadas = [];

  texto = '';


  constructor(
    private modal: ModalController,
    private apiUsuario: UsuariosLoginService,
    private toast: ToastServices,
    private almacenamiento: InicioService,
    private apiPatinada: PatinadasService,
    private socket: Socket
  ) { }

  ngOnInit() {}

  ionViewWillEnter() {
    this.listarPaises();
  }

  
  cerrar() {
    this.modal.dismiss();
  }


   
  async listarPaises() {

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.apiUsuario.listarPaises().subscribe((data: any) => {
        if (data.exe) {
          this.paises = data.response;


          console.log(this.paises, this.pais)
        } else {
          this.paises.push('Colombia');
        }
      }, (err) => {
        this.paises.push('Colombia');
      })
    }
    
  }


  listarCiudades( event ) {
    this.miciudad = this.miciudad !== '' ? this.miciudad : '';
    const _id = this.paises.filter((item) => item.nombre === event.detail.value)[0]._id;
    this.apiUsuario.listarCiudades(_id).subscribe((data: any) => {
     if (data.exe) {
       data.response.forEach((item) => {
          item.check = false;
          this.ciudades.push(item);
       })
       console.log(this.ciudades)
       this.ciudad = true;
     } else {
       this.toast.toastError('No se pudo traer las ciudades, intente registrarse ms tarde');
     }
   }, (err) => {
     this.toast.toastError('No se pudo traer las ciudades, intente registrarse ms tarde');
   })
 }

 seleccionarCiudad( event, i ) {
   this.ciudades[i].check = event.detail.checked;
   if (event.detail.checked) {
    if (!this.selecionadas.find((item) => item === event.detail.value)) {
      this.selecionadas.push(event.detail.value);
    }
   } else {
      this.selecionadas = this.selecionadas.filter((item) => item !== event.detail.value);
   }
   console.log(this.selecionadas);
 }

 cambiar(event ) {
    this.pais = '';
    this.selecionadas = [];
    if (event.detail.value === 'pais') {
      this.vista = 1;

    } else if (event.detail.value === 'ciudad') {
      this.vista = 2;
    } else {
      this.vista = 0;
    }
 }

 paissel(event ) {
  this.pais = event.detail.value;
}

buscar(event) {
  this.texto = event.detail.value;
}

escribir(event) {
  this.descripcion = event.detail.value;
}

async guardar() {

  if (this.descripcion !== '' && (this.pais !== '' || this.selecionadas.length > 0)) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      // tslint:disable-next-line:max-line-length
      this.apiPatinada.crearPatinada(info.token, {pais: this.pais, ciudad: this.selecionadas, descripcion: this.descripcion}).subscribe((data: any) => {
        if (data.exe) {
          this.toast.Especial('Patinada creada correctamente');
          console.log(data, 'midata')
          this.socket.emit('patinada', {
            dispositivos: data.response,
            usuarios: data.response2,
            nombre: info.nombre,
            _id: info._id
          })
          this.modal.dismiss({exe: true});
        } else {
          this.toast.toastError('No se pudo crer la patinada, intntelo ms tarde');
          this.modal.dismiss();
        }
      }, (err) => {
          this.toast.toastError('No se pudo crer la patinada, intntelo ms tarde');
          this.modal.dismiss();
      })
    }
  } else {
    this.toast.toastError('Verifique que tenga la descripcin de la patinada, el pas o ciudad de notificacin')
  }
  
}



}
