

import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filtrociudad'
})


export class FiltroCiudad implements PipeTransform {
    
    transform(listado: any[], texto: string) {
       // console.log(listado);
        if (texto === '') {
            return listado;
        } else {
            return listado.filter((item) => {
                let temp = item.nombre.toLowerCase();
                return temp.includes(texto);
            });
        }
       

    }
}
