  async crearBaseDeDatosSQLite() {
      const login = await this.storage.get('login');
      const citas = await this.storage.get('citas');
      const sala = await this.storage.get('sala');
      const ticket = await this.storage.get('tickets');

      if (login) {
        this.comprobarSesion().then(res => {
          this.sesion.next(res);
        });
      } else {
        this.comprobarSesion().then(res => {
          this.sesion.next(res);
        });
        await this.storage.set('login', []);
      }

      if (sala) {
        await this.storage.set('sala', []);
      } else {
        await this.storage.set('sala', []);
      }

      if (citas) {
      } else {
        await this.storage.set('citas', []);
      }

      if (ticket) {
      } else {
        await this.storage.set('tickets', []);
      }

}

    async comprobarSesion() {
    const login: any[] = await this.storage.get('login');

    if (login) {

      const data = login.filter((item: any) => {
        return item.estado === 'true';
      });

      if (data.length > 0) {
        return false;
      } else {
        return true;
      }
    } else {
      return false;
    }
  }

  cambiarEstado(status) {
       this.sesion.next(status);
  }


    async guardarUsuario(datos) {

      const login: any[] = await this.storage.get('login');

      if (login) {
  
        const data = login.filter((item: any) => {
          return item.nusuario === datos.nombreusuario;
        });
  
        if (data.length > 0) {

          let beforeData = login.filter((item: any) => {
            return item.nusuario !== datos.nombreusuario;
          });

          beforeData.push({
            _id: datos._id,
            nombre: datos.nombrecompleto,
            nusuario: datos.nombreusuario,
            clave: datos.clave,
            telefono: datos.tel,
            correo: datos.correo,
            role: datos.role,
            token: datos.token,
            pais: datos.pais,
            ciudad: datos.ciudad,
            usuarioID: datos.usuarioID,
            placa: datos.placa,
            estado: 'true',
          });

          await this.storage.set('login', beforeData);
          
    
        } else {

          login.push({
            _id: datos._id,
            nombre: datos.nombrecompleto,
            nusuario: datos.nombreusuario,
            clave: datos.clave,
            telefono: datos.tel,
            correo: datos.correo,
            role: datos.role,
            token: datos.token,
            pais: datos.pais,
            ciudad: datos.ciudad,
            usuarioID: datos.usuarioID,
            placa: datos.placa,
            estado: 'true',
          });

          await this.storage.set('login', login);
        }
      } else {
        return false;
      }

 }

   async reRenerarToken(nusuario, token) {


      const login: any[] = await this.storage.get('login');

      if (login) {
  
        const data = login.filter((item: any) => {
          return item.nusuario === nusuario;
        });
  
        if (data.length > 0) {

          let beforeData = login.filter((item: any) => {
            return item.nusuario !== nusuario;
          });

          beforeData.push({
            token,
          });

          await this.storage.set('login', beforeData);
        } 
      } else {
        return false;
      }
  }


