 async terminarPartida() {
  const login: any[] = await this.storage.get('login');
  
  if (login) {

    login.forEach((item: any) => {
      item.estado = 'false';
    });

    const cerrar = await this.storage.set('login', login);
    
    if ( cerrar ) {
      return true;
    } else {
      return false;
    }

  }
 }

