import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';

@Component({
  selector: 'app-modal-megustas',
  templateUrl: './modal-megustas.component.html',
  styleUrls: ['./modal-megustas.component.scss'],
})
export class ModalMegustasComponent implements OnInit {

  @Input() listado;


  constructor(
    private almacenamiento: InicioService,
    private modal: ModalController,
    private router: Router
  ) { }

  ngOnInit() {}

  ionViewWillEnter() {
    console.log(this.listado)
  }

 
 async perfil(usuario) {
  const info = await this.almacenamiento.obtenerToken();

  if ( info ) {
      if (info.nusuario === usuario) {
     
        this.router.navigate(['/perfil/', 's'])
        this.cerrar();  
      } else {
        this.router.navigate(['/visita/', usuario, 's'])
        this.cerrar();  
      }
  }
 }


  cerrar() {
    this.modal.dismiss();
  }

}
