import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'fchat'
})


export class FiltroChatAccesos implements PipeTransform {
    
    transform(listado: any[], arr: any[], tipo) {
        const resultado = [];
        if (tipo === 1) {
            arr.forEach((item => {
                let miarr = listado.filter((acc: any) => acc._id === item);
                if (miarr.length > 0) {
                    resultado.push(miarr[0].nombrecompleto);
                }
            }))
        } else {
            listado.forEach((item => {
                let miarr = arr.filter((acc: any) => acc === item._id);
                if (miarr.length === 0) {
                    resultado.push(item.nombrecompleto);
                }
            }))
        }

       // console.log(listado, 'otro', arr, 'listado')
        return resultado;
    }
}
