import { Component, Input, OnInit } from '@angular/core';
import { AlertController, LoadingController, ModalController } from '@ionic/angular';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { InventariosService } from 'src/app/providers/Api/Inventarios.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import { PedidosService } from '../../providers/Api/Pedidos.service';

@Component({
  selector: 'app-modal-mapas',
  templateUrl: './modal-mapas.component.html',
  styleUrls: ['./modal-mapas.component.scss'],
})
export class ModalMapasComponent implements OnInit {

  empleados = [];
  incluidos = [];
  accesos = [];

  load = false;

  marcar = false;
  id = '';

  ivt: any = {};
  cargauno = false;

  @Input() pedido;
  @Input() sala;
  constructor(
    private almacenamiento: InicioService,
    private IVTapi: InventariosService,
    private toast: ToastServices,
    private loading: LoadingController,
    private modal: ModalController,
    private apiPedido: PedidosService
  ) { }

  ngOnInit() {
  }

  async ionViewWillEnter() {

    if (this.sala) {
      await this.traerEmpleados2()
    } else {
      await this.traerEmpleados()
    }
    
  }

  refrescando( event) {
    this.traerEmpleados().then(() => {
      event.target.complete();
    }).catch(() => {
      event.target.complete();
    });
  }



  async traerEmpleados() {
    this.load = true;
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
        
        this.IVTapi.leerEmpleados(info.token).subscribe((data: any) => {
            console.log(this.pedido);
            if (data.exe) {
              data.response.forEach((item) => {
                if (item.usuario.imagen[0].path.includes('notfound')) {

                  item.usuario.imagen = '/assets/notfound.png';
                } else {
                  item.usuario.imagen = `https://motocaliapp.com/${item.usuario.imagen[0].path}`;

                  
                }



                if (this.pedido.accesos.filter((acceso) => acceso._id === item.usuario._id).length === 0) {
                  this.empleados.push(item);
                } else {
                  this.incluidos.push(item);
                }
              })
             


            } else {
              this.toast.toastError('No se pudo traer la informacin, intentelo ms tarde');
            }

            this.load = false;
        }, (err) => {
          this.toast.toastError('No se pudo traer la informacin, intentelo ms tarde');
        })

      }
  }

  
  async traerEmpleados2() {
    this.load = true;
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
        
        this.IVTapi.leerEmpleados(info.token).subscribe((data: any) => {
           
            if (data.exe) {
              data.response.forEach((item) => {
                if (item.usuario.imagen[0].path.includes('notfound')) {

                  item.usuario.imagen = '/assets/notfound.png';
                } else {
                  item.usuario.imagen = `https://motocaliapp.com/${item.usuario.imagen[0].path}`;

                  
                }

                console.log('INCLUIDOS', this.sala.accesos)

                const arr = this.sala.accesos.filter((acceso) => acceso._id === item.usuario._id);

                if (arr.length === 0) {
                  this.empleados.push(item);
                } else {
                  this.incluidos.push(item);
                }
              })
             


            } else {
              this.toast.toastError('No se pudo traer la informacin, intentelo ms tarde');
            }

            this.load = false;
        }, (err) => {
          this.toast.toastError('No se pudo traer la informacin, intentelo ms tarde');
        })

      }
  }


  async quitar(empleado, i) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        // tslint:disable-next-line:max-line-length
       if (this.pedido) {
        let nuevo = this.pedido.accesos.map((item) => {
          return item._id;
        });

        nuevo = nuevo.filter((item) => {
          return item !== empleado.usuario._id;
        })
        this.apiPedido.editarPedido(info.token, {id: this.pedido._id, accesos: nuevo}).subscribe((data: any) => {
           // console.log(data)
            if (data.exe) {
              this.empleados.push(empleado);
              this.incluidos.splice(i, 1);
            this.toast.Especial('Empleado excluido de esta sala');
            } else {
              this.toast.toastError('No se pudo realizar la peticin, intntelo ms tarde');
              this.cerrar()
            }
        }, (err) => {
              this.toast.toastError('No se pudo realizar la peticin, intntelo ms tarde');
              this.cerrar()
        })
       } else {
        let nuevo = this.sala.accesos.map((item) => {
          return item;
        });

        nuevo = nuevo.filter((item) => {
          return item._id !== empleado.usuario._id;
        })
        this.apiPedido.editarChat(info.token, {id: this.sala._id, accesos: nuevo}).subscribe((data: any) => {
           // console.log(data)
            if (data.exe) {
              this.empleados.push(empleado);
              this.incluidos.splice(i, 1);
              this.toast.Especial('Empleado excluido de esta sala');
            } else {
              this.toast.toastError('No se pudo realizar la peticin, intntelo ms tarde');
              this.cerrar()
            }
        }, (err) => {
              this.toast.toastError('No se pudo realizar la peticin, intntelo ms tarde');
              this.cerrar()
        })
       }
      }
  }

  async agregar(empleado, i) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        // tslint:disable-next-line:max-line-length
       if (this.pedido) {
        let nuevo: any[] = this.pedido.accesos.map((item) => {
          return item._id;
        });

        nuevo.push(empleado.usuario._id);
        console.log(nuevo, 'nuevo')
        this.apiPedido.editarPedido(info.token, {id: this.pedido._id, accesos: nuevo}).subscribe((data: any) => {
           // console.log(data)
            if (data.exe) {
            
            this.toast.Especial('Empleado aadido a esta sala');
            this.incluidos.push(empleado);
            this.empleados.splice(i, 1);

            } else {
              this.toast.toastError('No se pudo realizar la peticin, intntelo ms tarde');
              this.cerrar()
            }
        }, (err) => {
              this.toast.toastError('No se pudo realizar la peticin, intntelo ms tarde');
              this.cerrar()
        })
       } else {
        let nuevo: any[] = this.sala.accesos.map((item) => {
          return item;
        });

        nuevo.push(empleado.usuario._id);
        console.log(nuevo, 'nuevo')
        this.apiPedido.editarChat(info.token, {id: this.sala._id, accesos: nuevo}).subscribe((data: any) => {
           // console.log(data)
            if (data.exe) {
            
            this.toast.Especial('Empleado aadido a esta sala');
            this.incluidos.push(empleado);
            this.empleados.splice(i, 1);

            } else {
              this.toast.toastError('No se pudo realizar la peticin, intntelo ms tarde');
              this.cerrar()
            }
        }, (err) => {
              this.toast.toastError('No se pudo realizar la peticin, intntelo ms tarde');
              this.cerrar()
        })
       }
      }
  }

  cerrar() {
    this.modal.dismiss();
  }

  ionViewDidLeave() {
    // console.log('sale');
    this.cargauno = false;
    this.empleados = [];
  }
}
