import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanActivate, Router } from '@angular/router';
import { Observable, of, from, Subject, BehaviorSubject, ReplaySubject, AsyncSubject, throwError, timer } from 'rxjs';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { map, catchError, first, last, delay, auditTime, skipWhile, retryWhen, delayWhen, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class InicioGuard implements CanActivate {
  
  private sesion = new BehaviorSubject<any>(true);
  public $sesion = this.sesion.asObservable().pipe(auditTime(3000));

  constructor(
    private inicioService: InicioService,
    private route: Router,
  ) { // nadie se atreva a llorar dejen qu eria en silencio
    
  }


  async canActivate(): Promise<any> {
      
      try {
        const login = await this.inicioService.comprobarSesion();

        if (login) {
          
          return true;
        } else {
          this.route.navigate(['/central']);
          return false;
        }
      } catch (error) {
          this.route.navigate(['/central']);
          return false;
      }
  }


}
