import { Injectable } from '@angular/core';
import { InicioService } from '../Almacenamiento/inicio.service';
import { delayWhen, map, take, retryWhen } from 'rxjs/operators';
import { timer, Observable } from 'rxjs';
import { HttpErrorResponse, HttpHeaders, HttpClient } from '@angular/common/http';
import { NavController } from '@ionic/angular';
import { URL_SERVER } from 'src/environments/environment';

@Injectable({
    providedIn: 'root'
})

export class ApiSeguimientoService {

    constructor(
        private almacenamiento: InicioService,
        private navCtrl: NavController,
        private http: HttpClient
    ) { }

    private handlerError(error: Observable<any>) {


        return error.pipe(
            delayWhen(() => timer(3000)),
            map((errorHttp: HttpErrorResponse) => {

                
                    if (errorHttp instanceof ErrorEvent) {
                        throw new Error('Error de Red');
                    } else {

                        if ( errorHttp.status === 404) {
                           throw new Error(' al realizar la solicictud ');
                        } else if ( errorHttp.status === 401) {
                            try {
                                this.almacenamiento.terminarPartida().then(() => {
                                    this.navCtrl.navigateRoot('/home');
                                });
                            } catch (error) {
                                throw new Error('No tiene permisos para realizar la peticin ');
                            }
                            throw new Error('No tiene permisos para realizar la peticin ');
                         } else {
                           throw new Error(' No se pudo realizar la conexin, comprueba tu red ');
                        }
                       
                    }
            })
        );
}


    public solicitudesRecibidas(token) {
        const options = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.get(`${URL_SERVER}/seguimiento/usuarios/`, options).pipe(
            retryWhen(error => this.handlerError(error)),
            
        );
    }

    public enviarSolicitud(data: any) {
        const options = {
            headers: new HttpHeaders({
                'x-token': data.token
            })
        };

        return this.http.post(`${URL_SERVER}/seguimiento`,
        {
            IDreceptor: data.IDreceptor
        }, options).pipe(
            retryWhen(error => this.handlerError(error))
        );
    }

    public consultarSolicitud(token, idReceptor) {
        const options = {
            headers: new HttpHeaders({
                'x-token': token,
                'x-idReceptor': idReceptor
            })
        };

        return this.http.get(`${URL_SERVER}/seguimiento/consultar/`, options).pipe(
            retryWhen(error => this.handlerError(error))
        );

    }

    public cancelarSolicitud(token, idReceptor, type) {

        const options = {
            headers: new HttpHeaders({
                'x-token': token,
                'x-idReceptor': idReceptor,
                'x-tipo': type
            })
        };

        return this.http.delete(`${URL_SERVER}/seguimiento/usuario/`, options).pipe(
            retryWhen(error => this.handlerError(error))
        );
    }

    public aceptarSolicitud(token, idReceptor) {
        console.log('Datos ', token, idReceptor);
        const options = {
            headers: new HttpHeaders({
                'x-token': token,
                'x-idReceptor': idReceptor
            })
        };

        return this.http.put(`${URL_SERVER}/seguimiento/usuario/`, {}, options).pipe(
            retryWhen(error => this.handlerError(error))
        );
    }

    public solicitudesAceptadas(token, idReceptor, type) {
     //   console.log(type, idReceptor, 'LLamando a todo');
        const options = {
            headers: new HttpHeaders({
                'x-token': token,
                'x-idReceptor': idReceptor,
                'x-tipo': type
            })
        };

        return this.http.get(`${URL_SERVER}/seguimiento/usuarios/aceptados/`, options).pipe(
            retryWhen(error => this.handlerError(error))
        );
    }


    public solicitudesAceptadasSeguidos(token, idReceptor, type) {
       // console.log(type, idReceptor, 'LLamando a todo');
        const options = {
            headers: new HttpHeaders({
                'x-token': token,
                'x-idReceptor': idReceptor,
                'x-tipo': type
            })
        };

        return this.http.get(`${URL_SERVER}/seguimiento/usuarios/aceptados/seguidos/`, options).pipe(
            retryWhen(error => this.handlerError(error))
        );
    }

}
