import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanDeactivate } from '@angular/router';
import { Observable } from 'rxjs';
import { RegistroService } from '../../providers/Registro/registro.service';
import { map, find, last } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class RegistroGuard implements CanDeactivate<boolean> {
  
  constructor(
    private registroService: RegistroService
  ) { }
  canDeactivate(): Observable<any> {
    return this.registroService.observer.pipe(
      find(x => {
        return x !== 'a';
      }),
      map(x => { console.log(`ESTADO ${x}`); return x } )
    );
  }
  
}
