import { Injectable } from '@angular/core';
import { URL_SERVER } from '../../../environments/environment';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { map, retryWhen, delayWhen, take, catchError } from 'rxjs/operators';
import { timer, Observable, from } from 'rxjs';
import { FileTransferObject, FileTransfer, FileUploadOptions } from '@ionic-native/file-transfer/ngx';
import { Router } from '@angular/router';


@Injectable({
    providedIn: 'root'
})

export class PatinadasService {

    constructor(private http: HttpClient, private router: Router, private MmyTransfer: FileTransfer) { }

    private hanflerError(error: Observable<any>) {

            let intento = 0;

            return error.pipe(
                delayWhen(() => timer(3000)),
                map((errorHttp: HttpErrorResponse) => {

                        console.log('Entramos a intentos');
                        if (errorHttp instanceof ErrorEvent) {
                            throw new Error('Error de Red');
                        } else {

                            if ( errorHttp.status === 404) {
                               throw new Error(' Pgina no encontrada');
                            } if ( errorHttp.status === 401) {
                                throw new Error(' No tiene permisos ');
                             } else {
                                throw new Error(' No se pudo realizar la conexin, comprueba tu red ');
                            }
                           
                        }
                }),
                take(3)
            );
    }

  



    crearPatinada(token: any, data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };
        return this.http.post(`${URL_SERVER}/patinadas/crear`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    consultarPatinada(token: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };
        return this.http.post(`${URL_SERVER}/patinadas/traer`, {}, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    consultarPatinadaMias(token: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };
        return this.http.post(`${URL_SERVER}/patinadas/traer/mios`, {}, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


    patinadaEditar(token: any, data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };
        return this.http.post(`${URL_SERVER}/patinadas/editar`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


    
    patinadaEliminar(token: any, data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };
        return this.http.post(`${URL_SERVER}/patinadas/eliminar`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    consultarChat(token: any, data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/patinada/consultar/chat`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


    consultarChatUsuario(token: any, data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/patinada/consultar/chat/usuario`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

}
