import { Injectable }  from '@angular/core';
import { ToastController, NavController } from '@ionic/angular';
import { Router } from '@angular/router';
import { InicioService } from '../Almacenamiento/inicio.service';

@Injectable({
    providedIn: 'root'
})

export class ToastServices {
    
    constructor(
        private toasCTRL: ToastController,
        private router: Router,
        private navCtrl: NavController,
        private almacenamiento: InicioService
    ) { }

    async alertApplication(mensaje, estado: boolean, location?, buttons?) {

        if (!location || typeof location === 'undefined') {
            location = 'bottom';
        } else if (location === 'm') {
            location = 'middle';
        }
        let boton = []
        if (!buttons || typeof buttons === 'undefined') {
            boton = [];
        } else {
            boton = [{
                text: 'Navegar',
                handler: () => {
                    if (buttons.action) {
                        if (buttons.info.type === 'citas') {
                            this.almacenamiento.insertarCita(buttons.info.data.id).then(() => {
                                console.log('agregado')
                                this.router.navigate(['/chat', buttons.info.data.id ])
                            })
                        }
                    }
                }
            }];
        }

        const toast = await this.toasCTRL.create({
            message: mensaje,
           // cssClass: estado ? 'msg-ok' : 'msg-not',
            position: location,
            header: 'Mensaje de Mimoto',
            duration: 5000,
            buttons: boton,
            mode: 'ios'
        })

        return await toast.present();
    }


    async toastError(mensaje) {
        const toas = await this.toasCTRL.create({
            cssClass: 'errorAPP',
            position: 'bottom',
            duration: 3000,
            message: mensaje,
            header: 'Mensaje de la aplicacin'
        })

        return await toas.present();
    }


    async toastExitoso(mensaje, duration?, buttons?) {
        const toas = await this.toasCTRL.create({
            cssClass: 'exitoApp',
            position: 'bottom',
            duration: duration ? duration : 3000,
            message: mensaje,
            buttons,
            header: 'Mensaje de la aplicacin'
        })

        return await toas.present();
    }

    async toastNotificacion(mensaje, duration?, buttons?) {
        const toas = await this.toasCTRL.create({
            cssClass: 'notiApp',
            position: 'bottom',
            duration: duration ? duration : 3000,
            message: mensaje,
            buttons,
            header: 'Mensaje de la aplicacin'
        })

        return await toas.present();
    }

    async Especial(mensaje, duration?, buttons?) {
        const toas = await this.toasCTRL.create({
            cssClass: 'notiAppDark',
            position: 'bottom',
            duration: duration ? duration : 5000,
            message: mensaje,
            buttons,
            header: 'Tienes un nuevo mensaje'
        })

        return await toas.present();
    }
}
