import { Injectable } from '@angular/core';
import { Platform } from '@ionic/angular';
import { Socket } from 'ngx-socket-io';
import { InicioService } from '../Almacenamiento/inicio.service';
import { ToastServices } from '../Utilidades/ToastService.service';
import { Observable, BehaviorSubject } from 'rxjs';
import { EntradasServices } from '../cargas/Entradas.service';
import { Router } from '@angular/router';
@Injectable({
    providedIn: 'root'
})

export class ConexionService {

    public carga = false;
    private conexionBh = new BehaviorSubject('');
    public $conexiones = this.conexionBh.asObservable();

    constructor(
        private plt: Platform,
        private socket: Socket,
        private storage: InicioService,
        private toast: ToastServices,
        private entradas: EntradasServices,
        private router: Router
    ) {

        this.escucharFondo()

        this.escucharEntrada();

        this.conexiones().subscribe((data) => {
            this.conexionBh.next(data);
        })

        this.notificaciones().subscribe(async (data: any) => {
            const audio = new Audio('/assets/Timbre/Timbre.mp3');
                audio.play();
            if (data.action === 'citaschat') {
                const cita = await this.storage.obtenerCita();
                let buttons;
                let marcado = false;
                if (cita) {
                    console.log(cita, 'Mi cita');
                    if (cita !== data.idCita) {
                        marcado = true;
                       
                    }
                } else {
                    buttons = {
                        action: true,
                        info: {
                            type: 'citas',
                            data: {
                                id: data.idCita
                            }
                        },
                        ruta: '/chat'
                    };
                    this.toast.alertApplication(data.mensaje, true, 'bottom',  buttons);
                }

            } else if (data.action === 'recargo') {
               
                this.entradas.crearEntrada();
                console.log('LLego')

            } else if (data.action === 'salidaIVT') {
                this.toast.Especial(data.mensaje, 10000, [{
                    text: 'Navegar',
                    handler: () => {
                        this.router.navigate(['/central/menu/ivt-miinventario/', data.id, 'salidas', data.id, data.creador])
                    }
                }])
                console.log('LLego')
            }  else if (data.action === 'entradaIVT') {
                this.toast.Especial(data.mensaje, 10000, [{
                    text: 'Navegar',
                    handler: () => {
                        this.router.navigate(['/central/menu/ivt-miinventario/', data.id, 'entradas', data.id])
                    }
                }])
                console.log('LLego')
            } else if (data.action === 'megusta') {
                this.toast.Especial(data.mensaje, 10000, [{
                    text: 'Navegar',
                    handler: () => {
                        this.router.navigate(['/igtmpublic/', data.id, 'publicacion'])
                    }
                }])
            } else if (data.action === 'comentario') {
                this.toast.Especial(data.mensaje, 10000, [{
                    text: 'Navegar',
                    handler: () => {
                        this.router.navigate(['/igtmpublic/', data.id, 'publicacion'])
                    }
                }])
            } else if (data.action === 'Ticket') {
                const ticket = await this.storage.obtenerTicket();

                if (ticket) {
                    if (ticket.exe) {
                        const info = await this.storage.obtenerToken();

                        if (info) {
                            if (info._id === data.idtaller) {
                                this.toast.Especial(data.mensaje, 8000)
                            } else {
                                this.toast.Especial(data.mensaje2, 8000)
                            }

                        }

                        
                    } else {
                        const info = await this.storage.obtenerToken();

                        if (info) {
                            if (info._id === data.idtaller) {
                                this.toast.Especial(data.mensaje, 10000, [{
                                    text: 'Entrar',
                                    handler: () => {
                                        this.router.navigate(['/central/tickets/', data.idtaller])
                                    }
                                }])
                            } else {
                                this.toast.Especial(data.mensaje2, 5000, [{
                                    text: 'Entrar',
                                    handler: () => {
                                        this.router.navigate(['/central/tickets'])
                                    }
                                }])
                            }
                        }
                       
                    }
                }
               

            } else if (data.action === 'TicketRechazado') {
                const ticket = await this.storage.obtenerTicket();

                if (ticket) {
                    if (ticket.exe) {
                        const info = await this.storage.obtenerToken();

                        if (info) {
                            if (info._id !== data.idusuario) {
                                this.toast.Especial(data.mensaje, 8000)
                            } else {
                                this.socket.emit('ticketDespedida', {id: data.idtaller})
                                this.toast.Especial(data.mensaje2, 8000)
                            }

                        }

                    } else {

                        const info = await this.storage.obtenerToken();

                        if (info) {
                            if (info._id !== data.idusuario) {
                                this.toast.Especial(data.mensaje, 5000, [{
                                    text: 'Entrar',
                                    handler: () => {
                                        this.router.navigate(['/central/tickets'])
                                    }
                                }])
                            } else {
                                this.socket.emit('ticketDespedida', {id: data.idtaller})
                                this.toast.Especial(data.mensaje2, 5000, [{
                                    text: 'Entrar',
                                    handler: () => {
                                        this.router.navigate(['/central/tickets'])
                                    }
                                }])
                            }

                        }

                           

                    }
                }

            } else if (data.action === 'TicketProcesado') {
                const ticket = await this.storage.obtenerTicket();

                if (ticket) {
                    if (ticket.exe) {
                        const info = await this.storage.obtenerToken();

                        if (info) {
                            console.log(data, data.idusuario, info._id, 'careta')
                            if (info._id !== data.idusuario) {
                                this.toast.Especial(data.mensaje, 8000)
                            } else {
                                this.toast.Especial(data.mensaje2, 8000)
                            }

                        }

                    } else {

                        const info = await this.storage.obtenerToken();

                        if (info) {
                            if (info._id !== data.idusuario) {
                                this.toast.Especial(data.mensaje, 5000, [{
                                    text: 'Entrar',
                                    handler: () => {
                                        this.router.navigate(['/central/tickets'])
                                    }
                                }])
                            } else {
                                this.socket.emit('ticketDespedida', {id: data.idtaller})
                                this.toast.Especial(data.mensaje2, 5000, [{
                                    text: 'Entrar',
                                    handler: () => {
                                        this.router.navigate(['/central/tickets'])
                                    }
                                }])
                            }

                        }

                           

                    }
                }

            } else if (data.action === 'chat') {
                const sala: any = await this.storage.obtenerSala();

                if (sala) {

                    console.log(sala, 'Sala', 'entramos')

                    if (sala.length > 0) {

                 
                    if (sala[0]._id === data.idChat) {
                    } else {

                     
                                this.toast.Especial('Nuevo mensaje', 5000, [{
                                    text: 'Entrar',
                                    handler: () => {
                                        this.storage.insertarChat(data.sala).then(() => {
                                            this.router.navigate(['/central/chats/chat']);
                                          }).catch(() => {
                                            this.toast.toastError('No se puede acceder al chat, intntelo ms tarde');
                                          })
                                    }
                                }])

                    }
                } else {
                    console.log('Nada')
                    this.toast.Especial('Nuevo mensaje', 5000, [{
                        text: 'Entrar',
                        handler: () => {
                            this.storage.insertarChat(data.sala).then(() => {
                                this.router.navigate(['/central/chats/chat']);
                              }).catch(() => {
                                this.toast.toastError('No se puede acceder al chat, intntelo ms tarde');
                              })
                        }
                    }])
                }

            } 

            } else if (data.action === 'pedido') {
                this.toast.Especial(data.mensaje, 5000, [{
                    text: 'Ver',
                    handler: () => {
                            this.router.navigate(['/central/pedidos/info', data.id]);
                         
                    }
                }])
            } else if (data.action === 'patinada') {
                this.toast.Especial(data.mensaje, 5000, [{
                    text: 'Patinadas',
                    handler: () => {
                            this.router.navigate(['/central/patinadas']);
                    }
                }])
            } else if (data.action === 'patinadasmias') {
                this.toast.Especial(data.mensaje, 5000, [{
                    text: 'Ver',
                    handler: () => {
                            this.router.navigate(['/central/mispatinadas']);
                    }
                }])
            }
        });

    }

    async cargaLista() {
        try {
            this.carga = true;

        
            const login = await this.storage.obtenerToken();
            if ( login ) {
                console.log(login, ' Empezando')
                if (login.estado === 'true') {
                    this.socket.emit('conectar', login);
                } else {
                  //  this.toast.alertApplication('No se pudo iniciar la conexin', false);
                }

            }
        } catch (error) {
            this.toast.alertApplication('No se pudo iniciar la conexin', false);
        }
    }

    conexiones(): Observable<any> {
        
        const conx = new Observable((obs) => {
            this.socket.on('conexiones', (data) => {
                obs.next(data);
            })
        })

        return conx;
    }

    escucharFondo() {
        return this.plt.pause.subscribe( async (e) => {
                console.log('plataforma en fondo', e)
                const login = await this.storage.obtenerToken();

                if (login) {
                    console.log('login salida', login);
                    this.socket.emit('desconectar', login)
                }
        });
    }

    escucharEntrada() {
        return this.plt.resume.subscribe( async (e) => {
            if (this.carga) {
                console.log('plataforma en entrada', e)
                const login = await this.storage.obtenerToken();

                if (login) {
                    console.log('login entrada', login);
                    this.socket.emit('conectar', login);
                }
            }
        });

    }

    notificaciones() {
        const notificaciones = new Observable((obs) => {
            this.socket.on('notificacion', (data) => {
                console.log('mala', data)
                obs.next(data);
            })
        })

        return notificaciones;
    }

    async destruir(){
        try {
            const login = await this.storage.dataLogin();
            if ( login ) {
                console.log(login, ' Terminando')
                login.apagar = true;
                if (login.estado === 'false') {
                    this.socket.connect();
                    this.socket.emit('desconectar', login);
                    const cerrar = await this.storage.limpiarBD();

                    if (cerrar){
                        this.escucharEntrada().unsubscribe()
                        this.escucharFondo().unsubscribe();
                        return true;
                    } else {
                        return false;
                    }

                } else {
                    return false;
                }

        } else {
            return false;
        }
        } catch (error) {
            return false;
        }
       
    }
}
