import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanLoad, Router, CanActivate } from '@angular/router';
import { Observable, of, Subject, BehaviorSubject, AsyncSubject, ReplaySubject, throwError } from 'rxjs';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { map, catchError, first, last, delay, auditTime, skipWhile, retryWhen, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CentralGuard implements CanActivate {

private sesion = new BehaviorSubject<any>(true);
public $sesion = this.sesion.asObservable().pipe(auditTime(3000));

constructor(
  private inicioService: InicioService,
  private route: Router
) { // nadie se atreva a llorar dejen qu eria en silencio
 
}

  async canActivate(): Promise<any> {
    try {
      const login = await this.inicioService.comprobarSesion();
      console.log('Bien central')
      if (login) {
        console.log(login, ' central')
        this.route.navigate(['/home']);
        return false;
      } else {
        
        return true;
      }
    } catch (error) {
       return true;
    }
  }
}
