import { Injectable } from '@angular/core';
import { URL_SERVER } from '../../../environments/environment';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { map, concatAll, retryWhen, delayWhen, take } from 'rxjs/operators';
import { timer, Observable } from 'rxjs';

@Injectable({
    providedIn: 'root'
})

export class RecordatoriosService {

    constructor(private http: HttpClient) { }

    private hanflerError(error: Observable<any>) {

            let intento = 0;

            return error.pipe(
                delayWhen(() => timer(3000)),
                map((errorHttp: HttpErrorResponse) => {

                        console.log('Entramos a intentos');
                        if (errorHttp instanceof ErrorEvent) {
                            throw new Error('Error de Red');
                        } else {

                            if ( errorHttp.status === 404) {
                               // throw new Error(' Credenciales incorrectas, por favor comprueba la informacin ');
                            } else {
                               // throw new Error(' No se pudo realizar la conexin, comprueba tu red ');
                            }
                           
                        }
                }),
                take(3)
            );
    }

  

    postRecordatorio(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/recordatorios`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

   getRecordatorio(token: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.get(`${URL_SERVER}/recordatorios`, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    getRecordatorioPersona(token: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.get(`${URL_SERVER}/recordatorios/personas`, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    getRecordatorioLeer(token: any, data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.get(`${URL_SERVER}/recordatorios/leer`, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


    deleteRecordatorio(token: any, id: string) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token,
                'x-id': id
            })
        };

        return this.http.delete(`${URL_SERVER}/recordatorios`, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    putRecordatorio(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.put(`${URL_SERVER}/recordatorios`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }



    putRecordatorioPersona(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.put(`${URL_SERVER}/recordatorios/personas`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

}
