import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ToastServices } from '../../providers/Utilidades/ToastService.service';

@Component({
  selector: 'app-fechas-modal',
  templateUrl: './fechas-modal.component.html',
  styleUrls: ['./fechas-modal.component.scss'],
})
export class FechasModalComponent implements OnInit {

  fecha1 = '';
  fecha2 = '';

  constructor(
    private modal: ModalController,
    private toast: ToastServices
  ) { }

  ngOnInit() {}

  filtro ( event, tipo ) {

    if (tipo === 1) {
        this.fecha1 = event.detail.value;
    } else {
      this.fecha2 = event.detail.value;
    }
  }

  procesar() {
    if (this.fecha1 !== '' && this.fecha2 !== '') {
      this.modal.dismiss({
        fecha1: this.fecha1,
        fecha2: this.fecha2
      })
    } else {
      this.toast.toastError('Debe de seleccionar un tiempo estimado');
    }
  }

  cerrar() {
    this.modal.dismiss();
  }

}
