import { Component, OnInit, Input, ViewChild, ElementRef, AfterViewInit, HostListener, OnDestroy } from '@angular/core';
import { Observable } from 'rxjs';
import { IonRange } from '@ionic/angular';
import { trigger, transition, state, style, animate } from '@angular/animations';

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.scss'],
  animations: [
    trigger('entry', [
      state('closed', style({
        transform: 'scale(0)'
      })),
      state('open', style({
        transform: 'scale(1)'
      })),
      transition('* <=> *', [
        animate('50ms')
      ])
  ])
  ]
})
export class VideoComponent implements AfterViewInit, OnDestroy {
  
   @Input() url: any;
   @ViewChild('video', {static: true}) video: ElementRef;
   @ViewChild('range', {static: false}) range: ElementRef;
   paused: any = true;
   currProgress = 0;

   slideOpts  = {
    zoom: {
      maxRatio: 3,
      minRatio: 1,
      toggle: true
    },
   }

   currentt;
   focus = false;
   progress = 0;
   value = 0;
   bandera = true;
   carga = true;

   mostrar = false;
  constructor(
  ) { 
   
  }

  ngAfterViewInit() {
    this.current()
    console.log(this.url, ' archivo');
  }

  play() {
    this.paused = true;
    this.bandera = true;
    this.focus = true;
    this.video.nativeElement.play();
    
  }

  pause() {
    // console.log('Hola pause')
    this.paused = false;
    this.bandera = false;
    this.focus = false;
    this.video.nativeElement.pause();
   
  }

  current() {


        

        this.video.nativeElement.addEventListener('canplay', () => {
          this.carga = false;
          if (this.bandera) {
            this.play();
          }
         
        })

        this.video.nativeElement.addEventListener('progress', () => {
          this.carga = true;
        })

        this.video.nativeElement.addEventListener('timeupdate', () => {
          this.currProgress = (this.video.nativeElement.currentTime * 100) / this.video.nativeElement.duration;

          this.currentt = Math.round(this.video.nativeElement.currentTime);

          if (this.currentt < 10) {
            this.currentt = '0' + this.currentt;
          }

          if (this.video.nativeElement.currentTime === this.video.nativeElement.duration) {
           this.play();
         }
    })

  }

  toggle() {
    console.log(this.bandera, this.focus)
    if (this.bandera) {
      this.mostrar = true;
      this.bandera = false;
      this.pause()
    } else {
      this.mostrar = false;
      // console.log(this.focus);
      if (this.focus) {
        this.pause()
      } else {
        this.play()
      }
    }
    
  }


  progreso( event ) {
    this.progress = event.detail.value;
    
    if (this.focus) {
      this.play();
      this.focus = false;
    } 
    this.video.nativeElement.currentTime = Number((this.video.nativeElement.duration * (event.detail.value / 100)).toFixed(6));

  }

 foco(e) {
   this.bandera = true;
   this.focus = true;
   this.pause();
 }


  onProgress() {

  }

  ngOnDestroy() {
    this.video.nativeElement.pause();
    console.log('Destruccin')
  }

}
