import { Component, OnInit, Input } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { RecordatoriosService } from '../../providers/Api/Recordatorios.service';

@Component({
  selector: 'app-entradas',
  templateUrl: './entradas.component.html',
  styleUrls: ['./entradas.component.scss'],
})
export class EntradasComponent implements OnInit {

  @Input() type;
  @Input() data;

  titulo = '';
  contenido = '';
  creador = '';
  img = '';

  constructor(
    private modalCtrl: ModalController,
    private almacenamiento: InicioService,
    private apiRecordatorio: RecordatoriosService
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    console.log(this.data);
    this.titulo = this.data.informacion.titulo;
    this.contenido = this.data.informacion.contenido;
    this.creador = this.data.creador.nombrecompleto;
    this.img = this.data.creador.imagen.length > 0 ? this.data.creador.imagen[0].path : '';
  }

  async guardar() {
    const info =  await this.almacenamiento.obtenerToken();
    if ( info ) {
      console.log(info, this.data)
      const miUsuario = this.data.destinatarios.filter((item: any) => item.id === info._id);
      
      if ( miUsuario.length > 0 ) {
        
        const objeto = miUsuario[0];
        objeto.visto = 'SI';

        this.apiRecordatorio.putRecordatorioPersona(info.token, {_id: this.data._id, destino: objeto}).subscribe((data: any) => {
          this.cerrar();
          if (data.exe) {
            console.log('BIEN HECHO ', data);
          } else {

          }
        })
      }

    }
  }

  cerrar() {
      this.modalCtrl.dismiss({})
  }
}
