import { Component, OnInit, Console } from '@angular/core';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { LoadingService } from '../../providers/Utilidades/LoadingService.service';
import { ToastServices } from '../../providers/Utilidades/ToastService.service';
import { NavController, MenuController } from '@ionic/angular';
import { ApiSeguimientoService } from '../../providers/Api/Seguimiento.service';
import { CargaElementosUsuariosService } from '../../providers/cargas/usuarios.service';
import { Router } from '@angular/router';
import { ConexionService } from '../../providers/Conexiones/Conexion.service';

@Component({
    selector: 'app-menu',
    templateUrl: './menuComponent.component.html',
    styleUrls: ['./menuComponent.component.scss']
})

export class MenuComponent implements OnInit {

   public cantidadSeguimiento = 0;
   public red: string = 'wifi';

    constructor(
        private almacenamiento: InicioService,
        private loading: LoadingService,
        private toastService: ToastServices,
        private navCtrl: NavController,
        private apiSeguimiento: ApiSeguimientoService,
        private cargaElementos: CargaElementosUsuariosService,
        private router: Router,
        private menuCtrl: MenuController,
        private conexion: ConexionService
    ) {

      this.cargaElementos.$seguimientoCantidad.subscribe((cant) => {
        this.cantidadSeguimiento = cant;
        console.log(cant, 'Cantidad de seguimiento');
      });

      this.cargaElementos.$cargarRed.subscribe((red: any) => {
          console.log('Menu suscrbiendose', red);
          this.red = red;
      })

    }

    ngOnInit() {

    }

    async terminar() {
        try {
          const estado = await this.almacenamiento.terminarPartida();

          if (estado) {
            console.log(estado, 'ESTADO');
            this.loading.cargaProcesos('Cerrando sesin, te esperamos pronto').then( async () => {
              const abandonarPartida = await this.conexion.destruir(); 

              if (abandonarPartida) {
                console.log(abandonarPartida)
                console.log('Abandonar partida');
                this.cargaElementos.cancelarSuscripciones();
                this.router.navigate(['/home']);
                
                this.loading.finalizarCargado();
              } else {
                console.log('Abandonar partida fall');
                this.loading.finalizarCargado();
              }
             
            });

          } else {
            console.log('MUY MAL');
            this.router.navigate(['/home']);
          }

        } catch (error) {
          this.router.navigate(['/home']);
          this.loading.finalizarCargado();
          await this.toastService.alertApplication('No se pudo cerrar la sesin', false);
        }

      }

      ir() {
        this.menuCtrl.close('perfilMenu').then(() => {
          this.router.navigate(['/central/perfil/solicitudes']);
        })
      }

      citas() {
        this.menuCtrl.close('perfilMenu').then(() => {
          this.router.navigate(['/citas']);
        })
      }
}



