import { Pipe, PipeTransform  } from '@angular/core';
import * as moment from 'moment';

@Pipe({
    name: 'diferencia'
})

export class DiferenciaPipe implements PipeTransform {

    transform(fecha1: any, fecha2, tipo) {
        
        const fecha = moment(fecha1).format('DD-MM-YYYY HH:mm');
        const fechaf = moment(fecha2).format('DD-MM-YYYY HH:mm');
        console.log(fecha, fechaf)

        if (tipo === 1) {

            console.log(moment(fechaf).diff(moment(fecha), 'minutes'))
            
            if (moment(fechaf).diff(moment(fecha), 'minutes') > 60) {
                const msg = ((moment(fechaf).diff(moment(fecha), 'minutes')) / 60).toFixed();
                return msg + ' Horas';
            } else {
                const msg = moment(fechaf).diff(moment(fecha), 'minutes');
                return msg + ' Minutos';
            }
        } else {
            if (moment(moment().format('DD-MM-YYYY HH:mm')).diff(moment(fecha), 'minutes') > 60) {
                const msg = ((moment(moment().format('DD-MM-YYYY HH:mm')).diff(moment(fecha), 'minutes') )/ 60).toFixed();
                return msg + ' Horas';
            } else {
                const msg = moment(moment().format('DD-MM-YYYY HH:mm')).diff(moment(fecha), 'minutes');
                return msg + ' Minutos';
            }
        }
    }
}